import {mapState} from 'vuex';

export default {
    name: 'integral-model',
    computed: {
        ...mapState({
            appImg: state => state.mallConfig.__wxapp_img,
        }),
    },
    props: {
        text: {
            type: String,
            default: '0积分'
        },
    },
    data() {
        return {
            modelStatus: false,
            params: null,
        }
    },
    methods: {
        showModel(...params) {
            this.params = params;
            this.modelStatus = true;
        },
        next() {
            this.closeModel();
            this.$emit('next', this.params);
        },
        closeModel() {
            this.modelStatus = false;
        },
    }
}
